import React from 'react';
import ReactDOM from 'react-dom';
import  './index.css';

const quotesList = [
["In every walk with nature one receives far more than he seeks.", "John Muir"],
["The most beautiful gift of nature is that it gives one pleasure to look around and try to comprehend what we see.", "Albert Einstein"],
["Going to the mountains is going home.", "John Muir"],
["Over every mountain there is a path, although it may not be seen from the valley.", "Theodore Roethke"],
["There are always flowers for those who want to see them.", "Henri Matisse"],
["Nature does not hurry, yet everything is accomplished.", "Lao Tzu"],
["I felt my lungs inflate with the onrush of scenery—air, mountains, trees, people. I thought, ‘This is what it is to be happy.’", "Sylvia Plath"],
["Adopt the pace of nature: her secret is patience.", "Ralph Waldo Emerson"],
["Nature is not a place to visit. It is home.", "Gary Snyder"],
["In all things of nature there is something of the marvelous.", "Aristotle"],
["Instructions for living a life: Pay attention. Be astonished. Tell about it.", "Mary Oliver"],
["We do not see nature with our eyes, but with our understandings and our hearts.", "William Hazlitt"],
["Nature gives to every time and season some beauties of its own.", "Charles Dickens"],
["The Earth has music for those who listen.", "William Shakespeare"],
["If you truly love nature, you will find beauty everywhere.", "Vincent Van Gogh"],
["Nature's beauty is a gift that cultivates appreciation and gratitude.", "Louie Schwartzberg"],
["A walk in nature walks the soul back home.", "Mary Davis"]
];

function Buttons(props) {
return (
<div className="button">
  <button id="new-quote" onClick={props.changeQuote}>Get A New Quote</button>
  <a id="more-quotes-by-author" target="_blank" href={props.authorURL}>See More Quotes By The Same Author</a>
  <a id="tweet-quote" target="_blank" href={props.encodedURL}>Tweet Quote</a>
</div>
)
}

class Quote extends React.Component {
constructor(props) {
super(props);
this.state = {
  randomIndex: Math.floor(Math.random() * quotesList.length),
};
this.changeQuote = this.changeQuote.bind(this);
}

changeQuote() {
this.setState({
  randomIndex: Math.floor(Math.random() * quotesList.length),
});
}

render() {
const quote = quotesList[this.state.randomIndex][0];
const author = quotesList[this.state.randomIndex][1];
const encodedURL = "https://twitter.com/intent/tweet?text=" + encodeURIComponent(quote) + " - " + encodeURIComponent(author);
const authorSplitName = author.split(" ");
var authorURL = "https://www.brainyquote.com/authors/" + authorSplitName[0].toLowerCase();

if (authorSplitName.length > 1) {
  for (var i = 1; i < authorSplitName.length; i++) {
    authorURL += "_" + authorSplitName[i].toLowerCase();
  }
}

return (
  <div>
    <div className="quote">
      <blockquote id="text"><p>{quote}</p></blockquote>
      <br />
      <cite id="author">{author}</cite>
    </div>
    <Buttons changeQuote={this.changeQuote} authorURL={authorURL} encodedURL={encodedURL}/>
   </div>
);
}
}

class QuoteMachine extends React.Component {
render() {
return (
  <div className="container" id="quote-box">
    <Quote />
  </div>
);
}
}

ReactDOM.render(<QuoteMachine />, document.getElementById("root"));
